.open-ai {
    display: grid;
    height: 100%;
    overflow-y: auto;

    @include for-mobile-layout {
        grid-template-areas:
            "main"
            "footer";
        grid-template-rows: 1fr rem(100px);

        .chat-log {
            grid-area: main;
            padding: rem(8px) rem(10px);
            display: flex;
            flex-direction: column;
            gap: rem(8px) 0;

            span {
                align-self: center;
                margin: auto 0;
                font-weight: 600;
                font-size: rem(30px);
            }
        }
        .user-input {
            grid-area: footer;
            display: flex;
            flex-direction: column;
            position: sticky;
            bottom: 0;

            .input-top {
                height: 100%;
                input {
                    padding: rem(8px) rem(10px);
                    width: 100%;
                    height: 100%;
                    outline: none;
                    border: none;
                    border-top: rem(1px) solid rgb(158, 157, 157);
                    border-radius: rem(15px);
                    font-size: rem(16px);
                }
                ::-webkit-input-placeholder {
                    color: #a19e9e;
                }
            }
            .input-bottom {
                display: flex;
                align-content: center;
                justify-content: end;
                height: rem(50px);
                > * {
                    background: transparent;
                    border: none;
                    width: rem(48px);
                    height: rem(48px);
                }
            }
        }
    }

    #content h1 {
        color: #2c3e50;
    }

    #content h2 {
        color: #34495e;
    }

    #content ul {
        list-style-type: disc;
        margin-left: rem(20px);
    }

    #content p {
        font-size: 1.2em;
        line-height: 1.5em;
    }
}
.active {
    height: 100px;
    transition: height 20s ease-in-out 5s;
}
