.home-page {
    display: grid;
    height: 100%;
    width: 100%;
    font-family: ui-sans-serif, -apple-system, system-ui, "Segoe UI", Helvetica, "Apple Color Emoji", Arial, sans-serif, "Segoe UI Emoji", "Segoe UI Symbol";
    @include for-mobile-layout {
        grid-template-areas:
            "head head"
            "main main";
        grid-template-rows: rem(60px) 1fr;
        grid-template-columns: rem(150px) 1fr;

        .home-page-header {
            display: flex;
            gap: 0 rem(10px);
            padding: 0 rem(15px);
            //position: fixed;
            //top: 0;
            grid-area: head;
            text-align: center;
            align-items: center;
            img {
                height: rem(24px);
            }
            span {
                font-weight: 600; /* Slightly bold text */
                font-size: rem(20px);
            }
            button {
                margin-inline-start: auto;
                background-color: #000000; /* Greenish background similar to ChatGPT's sign up button */
                color: white; /* Text color */
                border: none; /* No border */
                border-radius: rem(50px); /* Rounded corners */
                height: rem(30px);
                width: rem(65px);
                font-size: rem(14px); /* Text size */
                font-weight: 00; /* Slightly bold text */

                cursor: pointer; /* Pointer cursor on hover */
            }
            button:focus,
            button:active {
                -webkit-tap-highlight-color: transparent;
            }
        }
        .open-ai {
            grid-area: main;
        }
        .side-menu {
            grid-area: main;
        }
    }
}
